import * as constants from './constants'
import axios from 'axios'
import { fromJS } from 'immutable'

const getSearchList = (data) =>{
    return {
        type: constants.GET_SEARCH_LIST,
        data: fromJS(data)
    }
}

export const searchFocus = () =>({
    type: constants.SEARCH_FOCUS
})
export const searchBlur = () =>({
    type: constants.SEARCH_BLUR
})
export const searchMouseIn = () =>({
    type: constants.SEARCH_MOUSE_IN
})
export const searchMouseOut = () =>({
    type: constants.SEARCH_MOUSE_OUT
})
export const searchListSwitch = (page) =>({
    type: constants.SEARCH_SWITCH,
    page
})
export const getSearch = () =>{
    return (dispatch) =>{
        axios.get('/api/search.json').then((res) =>{
            if (res.status === 200){
                dispatch(getSearchList(res.data.data))        
            }else {
                console.log('/api/search.json 请求失败!')
            }
        }).catch(() =>{
            // console.log('error')
        })
    }
}