import React, { Component } from 'react'
import {
    LunbotuWrapper,
    LunbotuArrow,
    LunbotuPoint
} from '../style'

class Lunbotu extends Component {
    constructor (props){
        super(props)
        this.state = {
            imgObj: [{
                id: 1,
                imgUrl: 'images/lunbotu1.jpg'
            }, {
                id: 2,
                imgUrl: 'images/lunbotu2.png'
            }, {
                id: 3,
                imgUrl: 'images/lunbotu3.jpg'
            }, {
                id: 4,
                imgUrl: 'images/lunbotu4.jpg'
            }, {
                id: 5,
                imgUrl: 'images/lunbotu5.jpg'
            }],
            index: 1
        }
        this.handleClickNext = this.handleClickNext.bind(this)
        this.handleClickPrev = this.handleClickPrev.bind(this)
        this.handleMouseOver = this.handleMouseOver.bind(this)
        this.autoPlay = this.autoPlay.bind(this)
    }
    render (){
        const { imgObj, index } = this.state
        return (
            <LunbotuWrapper>
                <ul>
                    {imgObj.map((item,i)=>(
                        <li key={item.id}
                            className={index===i?'curr':''}
                        ><img alt='' src={item.imgUrl} /></li>
                    ))}
                </ul>
                <LunbotuArrow className='arrow'>
                    <span className='left' onClick={this.handleClickPrev}>&lt;</span>
                    <span className='right' onClick={this.handleClickNext}>&gt;</span>
                </LunbotuArrow>
                <LunbotuPoint>
                    <ol>
                        {imgObj.map((item, i)=>(
                            <li key={i} className={index===i?'curr':''}
                                onMouseEnter={()=>{this.handleMouseOver(i)}}
                            >
                            </li>
                        ))}
                    </ol>
                </LunbotuPoint>
            </LunbotuWrapper>
        )
    }
    componentDidMount (){
        this.autoPlay()
    }
    handleClickNext (){
        let { index, imgObj } = this.state
        index += 1
        if (index>=imgObj.length){
            index = 0
        }
        this.setState({
            index
        })
    }
    handleClickPrev (){
        let { index, imgObj } = this.state
        index -= 1
        if (index<0){
            index = imgObj.length-1
        }
        this.setState({
            index
        })
    }
    handleMouseOver (curIndex){
        this.setState({
            index: curIndex
        })
    }
    autoPlay (){
        setInterval(() => {
            this.handleClickNext()
        }, 2000);
    }
}

export default Lunbotu