import { fromJS } from 'immutable'
import { constants } from '../store'

const defaultState = fromJS({
    topicList: [],
    articleList: [],
    recommentList: [],
    articlePage: 1,
    backTop: false
})

const initHomeData = (state, action)=>{
    return state.merge({
        topicList: fromJS(action.data.topicList),
        articleList: fromJS(action.data.articleList),
        recommentList: fromJS(action.data.recommendList)
    })
}
const addList = (state, action)=>{
    return state.merge({
        articlePage: action.curPage,
        articleList: state.get('articleList').concat(fromJS(action.addlist))
    })
}

export default (state = defaultState, action) => {
    switch (action.type) {
        case constants.INIT_HOME_DATA:
            return initHomeData(state, action)
        case constants.ADD_LIST:
            return addList(state, action)
        case constants.TOGGLE_SCROLL_TOP:
            return state.set('backTop', action.status)
        default:
            return state
    }
}