import styled from 'styled-components'

export const HomeWrapper = styled.div`
    width: 960px;
    margin: 0 auto;
    overflow:hidden;
`
export const HomeLeft = styled.div`
    width: 625px;
    float: left;
    margin-left: 15px;
    padding-top: 30px;
`
export const HomeRight = styled.div`
    width: 280px;
    height: 400px;
    float: right;
`

export const TopicWrapper = styled.div`
    padding: 20px 0 10px 0;
    overflow: hidden;
    border-bottom: 1px solid #dcdcdc;
`
export const TopicItem = styled.div`
    float: left;
    height 32px;
    line-height: 32px;
    background: #f7f7f7;
    font-size: 14px;
    color: #000;
    border: 1px solid #dcdcdc;
    border-radius: 4px;
    padding-right: 10px;
    margin-right: 18px;
    margin-bottom: 10px;
    img{
        padding-right: 10px;
        float: left;
        width: 32px;
        height: 32px;
    }
`

export const ListItem = styled.div`
    border-bottom: 1px solid #dcdcdc;
    overflow:hidden;
    padding:20px 0;
    img {
        float: right;
        width: 125px;
        height: 100px;
        border-radius: 4px;
    }
    &.test-enter{
        opacity: 0;
    }
    &.test-enter-active{
        opacity: 1;
        transition: all .6s ease;
    }
`
export const ListInfo = styled.div`
    float: left;
    width: 500px;
    overflow:hidden;
    h3 {
        font-size: 18px;
        line-height: 27px;
        font-weight: 700;
        color: #333;
        margin-bottom: 5px;
    }
    p {
        line-height: 24px;
        font-size: 13px;
        color:#999;
    }
`
export const ListMeta = styled.div`
    line-height: 24px;
    font-size: 12px;
    color:#999;
    a {
        margin-right: 10px;
        color:#999;
        text-decoration: none;
    }
    i {
        margin-right: 3px;
    }
    .icon-message {
        font-size: 14px;
    }
    .icon-lovecopy {
        font-size: 12px;
    }
`
export const ReadMore = styled.div`
    height: 40px;
    background: #a5a5a5;
    color: #fff;
    font-size: 15px;
    line-height: 40px;
    border-radius: 20px;
    text-align: center;
    margin-top: 30px;
    cursor: pointer;
`
export const RecommentWrapper = styled.div`
    margin-top: 30px;
`
export const RecommentItem = styled.a`
    display: block;
    img {
        width: 280px;
        height: 50px;
    }
    border-radius: 4px;
    margin-bottom: 8px;
    background-size: contain;
`
export const BackTop = styled.div`
    width: 50px;
    height: 50px;
    border: 1px solid #ccc;
    position: fixed;
    right: 40px;
    bottom: 40px;
    span {
        width: 15px;
        height: 15px;
        margin: 22px 0 0 17px;
        border-left: 1px solid #333;
        border-top: 1px solid #333;
        display: block;
        transform: rotate(45deg);
        cursor: pointer;
    }
`

export const DownloadWrapper = styled.div`
    width: 234px;
    padding: 10px 22px;
    border: 1px solid #f0f0f0;
    border-radius: 6px;
    cursor:pointer;
    position: relative;
    img {
        float: left;
        width: 60px;
        height: 60px;
    }
    .info {
        float: left;
        margin-left: 10px;
        margin-top: 10px;
        h3 {
            font-size: 15px;
            color: #333;
        }
        p {
            color: #999;
            font-size: 13px;
            margin-top:8px;
        }
    }
    .hover {
        display: none;
        width: 140px;
        height: 140px;
        background-color: #fff;
        position: absolute;
        left: 70px;
        top: -150px;
        box-shadow: 0 0 8px #ccc;
        border-radius: 6px;
        border: 1px solid #ccc;
        img {
            width: 120px;
            height: 120px;
            margin: 10px 0 0 10px;
        }
        &::after {
            content:'';
            display: block;
            width: 15px;
            height: 15px;
            background: #fff;
            position: absolute;
            transform: rotate(45deg);
            left: 62px;
            bottom: -10px;
            border-right: 1px solid #ccc;
            border-bottom: 1px solid #ccc;
        }
    }
    &:hover .hover {
        display: block;
    }
`
export const WriterWrapper = styled.div`
    width: 278px;
    border-radius: 3px;
    margin-top: 20px;
    .Writer-hd {
        font-size: 14px;
        color: #969696;
        line-height: 20px;
    }
    .Writer-con li {
        overflow: hidden;
        margin-bottom: 10px;
        img {
            float: left;
            width: 48px;
            height: 48px;
            border-radius: 50%;
            border: 1px solid #ccc;
        }
        .info {
            float: left;
            margin-left: 10px;
            h3 {
                font-size: 14px;
                margin-top:10px;
                margin-bottom: 5px;
                color: #333;
            }
            p {
                font-size: 13px;
                color: #969696;
            }
        }
        .attention {
            font-size: 13px;
            color: #42c02e;
            float: right;
        }
    }
    .Writer-more {
        height: 35px;
        line-height: 35px;
        text-align: center;
        color: #787878;
        background-color: #f7f7f7;
        border: 1px solid #dcdcdc;
        border-radius: 4px;
        cursor: pointer;
    }
`

export const LunbotuWrapper = styled.div`
    position:relative;
    width: 625px;
    height: 270px;
    background: #fff;
    overflow: hidden;
    cursor: pointer;
    ul {
        width: 100%;
        position: relative;
        li {
            position: absolute;
            left: 0;
            top: 0;
            opacity: 0;
            transition: all .6s ease;
            img {
                border-radius: 5px;
                width: 625px;
                height: 270px;
            }
        }
        li.curr{
            opacity: 1;
            z-index: 88;
        }
    }
    &:hover .arrow {
        display: block;
    }
`
export const LunbotuArrow = styled.div`
    z-index: 99;
    position: absolute;
    width: 100%;
    top: 50%;
    margin-top: -20px;
    display: none;
    span {
        width: 40px;
        height: 50px;
        text-align: center;
        line-height: 50px;
        color: #fff;
        font-size:20px;
        background-color: rgba(0,0,0,.4);
        &.left{
            float: left;
        }
        &.right{
            float: right;
        }
    }
`
export const LunbotuPoint = styled.div`
    position: absolute;
    z-index: 99;
    right: 0;
    bottom: 20px;
    left: 50%;
    margin-left: -67.5px;
    ol{
        overflow: hidden;
        li{
            float: left;
            width: 25px;
            height: 2px;
            margin: 1px;
            background-color: hsla(0,0%,47%,.4);
            &.curr {
                background-color: #fff;
            }
        }
    }
`