import React, { Component } from 'react'
import { connect } from 'react-redux'
import {
    RecommentWrapper,
    RecommentItem
} from '../style'

class Recomment extends Component {
    render (){
        const { recommentList } = this.props
        return (
            <RecommentWrapper>
                {recommentList.map((item)=>(
                    <RecommentItem key={item.get('id')}>
                        <img alt='' src={item.get('imgUrl')}/>
                    </RecommentItem>
                ))}
            </RecommentWrapper>
        )
    }
}

const mapState = (state)=>{
    return {
        recommentList: state.getIn(['home', 'recommentList'])
    }
}

export default connect(mapState, null)(Recomment)