import React, { PureComponent } from 'react'
import List from './components/List'
import Recomment from './components/Recomment'
import Topic from './components/Topic'
import Writer from './components/Writer'
import Lunbotu from './components/Lunbotu'
import Download from './components/Download'
import { connect } from 'react-redux'
import { actionCreator } from './store'
import {
    HomeWrapper,
    HomeLeft,
    HomeRight,
    BackTop
} from './style'

class Home extends PureComponent {
    render (){
        const { backTop } = this.props
        return (
            <HomeWrapper>
                <HomeLeft>
                    <Lunbotu />
                    <Topic />
                    <List />
                </HomeLeft>
                <HomeRight>
                    <Recomment />
                    <Download />
                    <Writer />
                </HomeRight>
                {backTop?<BackTop onClick={this.ScrollTop}><span></span></BackTop>: ''}
            </HomeWrapper>
        )
    }
    ScrollTop (){
        // window.scrollTo(0, 0)
        var tmp = document.documentElement.scrollTop
        var timer = setInterval(() => {
            tmp -=20
            document.documentElement.scrollTop = tmp
            if (tmp<=0){
                clearInterval(timer)
            }       
        }, 10);
    }
    bindEvents (){
        window.addEventListener('scroll', this.props.changgeScrollTop)
    }
    componentDidMount (){
        this.props.initHomeData()
        this.bindEvents()
    }
    componentWillUnmount (){
        window.removeEventListener('scroll', this.props.changgeScrollTop)
    }
}

const mapState = (state)=>{
    return {
        backTop: state.getIn(['home', 'backTop'])
    }
}

const mapDispatch = (dispatch)=>({
    initHomeData (){
        dispatch(actionCreator.getHomeInit())        
    },
    changgeScrollTop (){
        if (document.documentElement.scrollTop > 400){
            dispatch(actionCreator.toggleScrollTop(true))
        } else {
            dispatch(actionCreator.toggleScrollTop(false))
        }
    }
})

export default connect(mapState, mapDispatch)(Home)