import React, { Component } from 'react'
import { connect } from 'react-redux'
import {
    ListItem,
    ListInfo,
    ListMeta,
    ReadMore
} from '../style'
import { actionCreator } from '../store';
import { Link } from 'react-router-dom'
import { CSSTransition, TransitionGroup } from 'react-transition-group'

class List extends Component {
    render (){
        const { listPage, list, getMore } = this.props
        return (
            <div>
                <TransitionGroup>
                {list.map((item,index)=>(
                    <CSSTransition
                        key={index}
                        timeout={1000}
                        classNames='test'
                    >
                    <ListItem>
                        <img alt='' src={item.get('imgUrl')} />
                        <ListInfo>
                            <Link to={'/detail/'+item.get('id')}>
                                <h3>{item.get('title')}</h3>
                            </Link>
                            <p>{item.get('desc')}</p>
                            <ListMeta>
                                <a className='nickname' href='./'>楚宛</a>
                                <a className='message' href='./'>
                                    <i className='iconfont icon-message'></i>
                                    59
                                </a>
                                <span>
                                    <i className='iconfont icon-lovecopy'></i>
                                    559
                                </span>
                            </ListMeta>
                        </ListInfo>
                    </ListItem>
                    </CSSTransition>
                ))}
                </TransitionGroup>
                <ReadMore onClick={()=>{getMore(listPage)}}>阅读更多</ReadMore>
            </div>
        )
    }
}

const mapState = (state)=>{
    return {
        list: state.getIn(['home', 'articleList']),
        listPage: state.getIn(['home', 'articlePage'])
    }
}

const mapDispatch = (dispatch)=>{
    return {
        getMore (page){
            dispatch(actionCreator.addList(page))
        }
    }
}

export default connect(mapState, mapDispatch)(List)