import styled from 'styled-components'
import logoPic from '../../statics/images/logo.png'

export const HeaderWrapper = styled.div`
    height: 55px;
    position: relative;
    border-bottom: 1px solid #eee;
`
export const Logo = styled.div`
    width: 100px;
    height: 56px;
    float: left;
    background: url(${logoPic}) no-repeat;
    background-size: contain;
`
export const Menu = styled.div`
    width: 870px;
    margin: auto;
    height: 56px;
    font-size: 17px;
`
export const MenuItem = styled.div`
    cursor: pointer;
    text-align: center;
    line-height: 56px;
    color: #333;
    &.fl{
        float: left;
        padding-right: 20px;
    }
    &.fr{
        float:right;
        padding-left: 20px;
    }
    &.active{
        color: #ea6f5a;
    }
    i{
        font-size: 18px;
    }
`
export const SearchWrapper = styled.div`
    float: left;
    position: relative;
    .icon-fangdajing{
        position: absolute;
        right: 4px;
        top:12px;
        font-size: 18px;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        line-height: 32px;
        text-align: center;
    }
    &.focused .icon-fangdajing{
        background: #999;
        color: #fff;
    }   
`
export const SearchInfo = styled.div`
    width: 250px;
    background: #fff;
    position: absolute;
    top: 56px;
    box-shadow: 0 0 8px rgba(0,0,0,.2);
    padding: 20px;
    color: #969696;
    border-radius: 3px;
    z-index: 999;
`
export const SearchInfoH = styled.div`
    font-size: 14px;
    margin-bottom: 10px;
`
export const SearchInfoSwith = styled.div`
    font-size: 13px;
    float: right;
    cursor: pointer;
    .icon-spin {
        float:left;
        transition: all 1s ease;
        transform-origin: center center;
    }
`
export const SearchInfoList = styled.div`

`
export const SearchInfoItem = styled.div`
    float: left;
    padding: 2px 6px;
    font-size: 12px;
    border: 1px solid #ddd;
    margin-right: 10px;
    margin-top: 10px;
    border-radius: 3px;
`

export const MenuSearch = styled.input.attrs({
    placeholder: '搜索'
})`
    width: 140px;
    height 38px;
    outline: none;
    border: none;
    background-color: #eee;
    margin-top: 9px;
    border-radius: 19px;
    padding-left: 20px;
    &.slide-enter {
        transition: all .2s ease
    }
    &.slide-enter-active {
        width: 220px;
        transition: all .2s ease;
    }
    &.slide-exit {
        transition: all .2s ease;
    }

`

export const Addition = styled.div`
    position: absolute;
    right: 0;
    top: 0;
`
export const Button = styled.div`
   float: left;
   padding: 0 20px;  
   line-height: 38px;
   margin-top: 7px;
   border: 1px solid #ea6f5a;
   margin-right: 20px;
   border-radius: 20px;
   &.writting{
       background: #ea6f5a;
       color: #fff;
       i{
           font-size: 18px;
           margin-right: 5px;
       }
   }
`