import React, { Component } from 'react';
import {
    HeaderWrapper,
    Logo,
    Menu,
    MenuItem,
    Addition,
    Button,
    MenuSearch,
    SearchWrapper,
    SearchInfo,
    SearchInfoH,
    SearchInfoSwith,
    SearchInfoList,
    SearchInfoItem
} from './style'
import { CSSTransition } from 'react-transition-group'
import { connect } from 'react-redux'
import { actionCreator } from './store'
import { actionCreator as loginActionCreator } from '../../pages/login/store'
import { Link } from 'react-router-dom'

class Header extends Component {
    render (){
        const { isFocused, mouseIn, login, handleInputFocus, handleInputBlur, searchList, handleLogout } = this.props
        return (
            <HeaderWrapper>
            <Link to='/'><Logo /></Link>
            <Menu>
                <MenuItem className='fl active'>首页</MenuItem>
                <MenuItem className='fl'>下载App</MenuItem>
                <SearchWrapper className={isFocused?'focused':''}>
                    <CSSTransition
                       in={isFocused}
                       timeout={1000}
                       classNames='slide'
                    >
                        <MenuSearch 
                            onFocus={()=>{handleInputFocus(searchList)}}
                            onBlur={handleInputBlur}
                        ></MenuSearch>
                    </CSSTransition>
                    <i className='iconfont icon-fangdajing'></i>
                    {this.getListArea(isFocused, mouseIn)}
                </SearchWrapper>
                {
                    login?
                    <MenuItem className='fr' onClick={handleLogout}>退出</MenuItem>: 
                    <Link to='/login'>
                        <MenuItem className='fr'>登陆</MenuItem>
                    </Link>
                }
                
                <MenuItem className='fr'><i className='iconfont icon-Aa'></i></MenuItem>
            </Menu>
            <Addition>
                <Button>注册</Button>
                <Link to='/write'>
                    <Button className='writting'>
                        <i className='iconfont icon-maobi'></i>
                        写文章
                    </Button></Link>
            </Addition>
          </HeaderWrapper>
        )
    }

    getListArea (isFocused = false, mouseIn = false){
        const { handleMouseIn, handleMouseOut, handleSwitchClick, page, pageSize, searchList } = this.props
        const list = searchList.toJS()
        const start = (page-1)*pageSize
        const end = start + pageSize
        var tempList = []
        var newPage = page + 1
        if (newPage * pageSize >= list.length){
            newPage = 1
        }
        if (list.length){
            tempList = list.slice(start, end)
        }
        if (isFocused || mouseIn){
            return (
                <SearchInfo
                    onMouseEnter={handleMouseIn}
                    onMouseLeave={handleMouseOut}
                >
                    <SearchInfoH>
                        热门搜索
                        <SearchInfoSwith onClick={()=>{handleSwitchClick(newPage, this.spin)}}>
                        <i className='iconfont icon-spin'
                           ref={(spin)=>{this.spin = spin}}      
                        >
                        </i>
                        换一批
                        </SearchInfoSwith>
                    </SearchInfoH>
                    <SearchInfoList>
                        {tempList.map((item)=>(
                                <SearchInfoItem key={item}>{item}</SearchInfoItem>
                            ))
                        }
                    </SearchInfoList>
                </SearchInfo>
            )
        } else {
            return null
        }   
    }
}

const mapStateToProps = (state) =>{
    return {
        isFocused: state.getIn(['header', 'isFocused']),
        searchList: state.getIn(['header', 'searchList']),
        mouseIn: state.getIn(['header', 'mouseIn']),
        page: state.getIn(['header', 'page']),
        pageSize: state.getIn(['header', 'pageSize']),
        login: state.getIn(['login', 'login'])
    }
}

const mapDispatchToProps = (dispatch) =>{
    return {
        handleInputFocus (searchList){
            (searchList.size === 0) && dispatch(actionCreator.getSearch())
            dispatch(actionCreator.searchFocus())
        },
        handleInputBlur (){
            dispatch(actionCreator.searchBlur())
        },
        handleMouseIn (){
            dispatch(actionCreator.searchMouseIn())
        },
        handleMouseOut (){
            dispatch(actionCreator.searchMouseOut())
        },
        handleSwitchClick (page, spin){
            let Angle = spin.style.transform.replace(/[^0-9]/ig, '')
            if (!Angle){
                Angle = 360
            } else {
                Angle = 360 + parseInt(Angle)
            }
            spin.style.transform = 'rotate('+Angle+'deg)'
            dispatch(actionCreator.searchListSwitch(page))
        },
        handleLogout (){
            dispatch(loginActionCreator.changeLogout())
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Header);
