import styled from 'styled-components'

export const LoginWrapper = styled.div`
    position:absolute;
    top:56px;
    left:0;
    right:0;
    bottom:0;
    background:#eee;
`
export const LoginBox = styled.div`
    width: 400px;
    height: 180px;
    margin: 100px auto;
    background: #fff;
    padding-top: 20px;
    box-shadow: 0 0 8px rgba(0, 0, 0, .1);
    input {
        display: block;
        width: 200px;
        height: 30px;
        margin: 10px auto;
        padding: 0 10px;
        line-height: 30px;
        color: #777;
    }
    button {
        display: block;
        border: none;
        width: 220px;
        height: 30px;
        margin: 20px auto;
        background: #3194d0;
        text-align: center;
        line-height: 30px;
        border-radius: 15px;
        color: #fff;
    }
`
