import styled from 'styled-components'

export const DetailWrapper = styled.div`
    width: 620px;
    height: 300px;
    margin: 0 auto;
    padding: 20px 0 40px 0;
`
export const Title = styled.h3`
    font-size: 34px;
    font-weight: 700;
    line-height: 1.3;
    text-align: center;
`
export const Content = styled.div`
    color: #2f2f2f;
    img {
        width:100%;
    }
    p {
        font-size: 16px;
        line-height: 30px;
        margin: 25px 0;
        padding: 0 15px;
        b{
            font-weight: 700;
        }
    }
`