import React, { Component } from 'react'
import { connect } from 'react-redux'
import { actionCreator } from './store' 
import { Redirect } from 'react-router-dom'
import {
    LoginWrapper,
    LoginBox
} from './style'

class Login extends Component {
    render (){
        const { login, handleLogin }=this.props
        if (!login){
            return (
                <LoginWrapper>
                    <LoginBox>
                        <input id='name' type='text' placeholder='账号' ref={(name)=>{this.name=name}} />
                        <input id='password' type='password' placeholder='密码' ref={(password)=>{this.password=password}}/>
                        <button onClick={()=>{handleLogin(this.name,this.password)}}>登陆</button>
                    </LoginBox>
                </LoginWrapper>
            )
        } else {
            return <Redirect to='/' />
        }
    }
}

const mapState = (state)=>{
    return {
        login: state.getIn(['login', 'login'])
    }
}

const mapDispatch = (dispatch)=>{
    return {
        handleLogin (nameElem, passElem){
            dispatch(actionCreator.login(nameElem.value, passElem.value))
        }
    }
}

export default connect(mapState, mapDispatch)(Login)