import axios from 'axios'
import { constants } from './'

const homeInitAction = (data) =>({
    type: constants.INIT_HOME_DATA,
    data
})
const addListAction = (addlist, curPage) =>({
    type: constants.ADD_LIST,
    addlist,
    curPage
})
export const toggleScrollTop = (status)=>({
    type: constants.TOGGLE_SCROLL_TOP,
    status
})

export const getHomeInit = ()=>{
    return (dispatch)=>{
        axios.get('/api/home.json').then((res)=>{
            if (res.data.success){
                dispatch(homeInitAction(res.data.data))
            }
        })
    }
}

export const addList = (page)=>{
    return (dispatch)=>{
        axios.get('/api/homeList.json?page=' + (page+1)).then((res)=>{
            if (res.data.success){
                dispatch(addListAction(res.data.data, page+1))
            }
        })
    }
}
