import { constants } from './'
import { fromJS } from 'immutable'

const defaultState = fromJS({
    isFocused: false,
    mouseIn: false,
    searchList: [],
    page: 1,
    pageSize: 10
})

export default (state = defaultState, action) => {
    switch (action.type) {
        case constants.SEARCH_FOCUS:
            return state.set('isFocused', true)
        case constants.SEARCH_BLUR:
            return state.set('isFocused', false)
        case constants.SEARCH_MOUSE_IN:
            return state.set('mouseIn', true)
        case constants.SEARCH_MOUSE_OUT:
            return state.set('mouseIn', false)
        case constants.SEARCH_SWITCH:
            return state.set('page', action.page)
        case constants.GET_SEARCH_LIST:
            return state.set('searchList', action.data);
        default:
            return state
    }
}